html, body {
  min-width: 20rem;
  min-height: 100vh;
  width: 100%;
  position: relative;
  background-color: #eff0f1;
}

body {
  font-family: "Noto Serif", "Roboto", "system-ui";
  font-size: 16px;
  line-height: 1.5;
  color: rgb(109, 108, 119);
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

main {
  font-size: 16px;
  flex-grow: 1;
  margin-top: 1.5rem;
}

header, main, footer {
  flex-shrink: 0;
}

.page-header {
  border-bottom: solid .125rem #c9c9c9;
  padding-bottom: 10px;
  margin-bottom: 10px;
}

.page-body {
  padding-top: 1rem;
}

.subtitle {
  font-style: italic;
  font-size: 20px;
}

h2 {
  font-size: 32px;
}

h3 {
  font-size: 24px;
}

h4 {
  font-weight: bold;
  font-size: 20px;
}

hr {
  margin-top: 2rem;
  margin-bottom: 2rem;
  border-top: .15rem solid rgba(0,0,0,.1);
  width: 90%;
}

.table-div {
  padding-bottom: 1rem;
}

.table {
  color: #606c71;
}

tr {
  display: table-row;
  vertical-align: inherit;
  border-color: inherit;
  border-bottom: solid 1px;
  border-color: #dee2e6;
}

pre {
  padding: .8rem;
  margin-top: 0;
  margin-bottom: 1rem;
  font-family: "Roboto Mono", monospace;
  font-size: 14px;
  background-color: #e0e1e2;
  border: solid .0625rem #dce6f0;
  border-radius: .3rem;
  overflow: auto;
  width: 100%;
  -webkit-overflow-scrolling: touch;

  > code {
    padding: 0;
    margin: 0;
    font-size: 14px;
    word-break: normal;
    word-wrap: normal;
    background: transparent;
    border: 0;
  }
}

#anchor {
  position: relative;
}

#anchor a {
  position: absolute;
  top: -2.5rem;
}

.image {
  max-width: 100%;
  height: auto;
  padding-bottom: .625rem;
}

.four-0-four {
  height: 50%;
  padding-bottom: 2rem;
  padding-top: 2rem;
}

::-webkit-input-placeholder {
  color: #919090;
  opacity:1
}

.form-control::-moz-placeholder {
  color: #919090;
  opacity:1
}

.form-control:-ms-input-placeholder {
  color: #919090;
  opacity:1
}

.form-control::-ms-input-placeholder {
  color: #919090;
  opacity:1
}

.form-control::placeholder {
  color: #919090;
  opacity: 1;
}

footer {
  margin-top: 1rem;
  padding-top: .5rem;
  padding-bottom: .5rem;
  border-top: solid .125rem #c9c9c9;
  left: 0;
  bottom: 0;
  width: 100%;
  text-align: center;
  font-size: 12px;
}
