nav.navbar {
  background: linear-gradient(90deg, rgba(131,58,180,1) 0%, rgba(253,29,29,1) 50%, rgba(252,176,69,1) 100%); // rgb(86, 86, 90);

  .navbar-brand {
    font-weight: 700;
  }

  > .navbar-toggler {
    color: rgb(255, 255, 255);
    border-color: rgb(255, 255, 255);
  }

  .navbar-toggler[aria-expanded="false"] > .feather-x {
    display: none;
  }

  .navbar-toggler[aria-expanded="true"] > .feather-menu {
    display: none;
  }

  .navbar-nav > .nav-item > .nav-link {
    color: rgb(255, 255, 255);
    font-size: 1rem;
    padding-right: 1rem;
    padding-left: 1rem;
  }

  .navbar-nav > .nav-item.active > .nav-link {
    text-decoration: underline;
  }
}

nav.blue {
  background: #0054c4;
}

nav.purple {
  background: #2b0080;
}

nav.green  {
  background: #419258;
}

nav.red {
  background: #ab0101;
}
